@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@500;700&display=swap');

* {
	padding: 0;
	margin: 0;
	outline: none;
}

:root {
	--orange: #FF7F21;
	--darkGrey: #4A4A4A;
	--grey: #757575;
	--lightGrey: #E5E5E5;
}

a {
	text-decoration: none;
	font-size: 18px;
	line-height: 100%;
}

p {
	font-size: 14px;
	line-height: 180%;
	margin-bottom: 10px;
	&:last-child {
		margin-bottom: 0;
	}
	&:last-of-type {
		margin-bottom: 0;
	}
}

ul {
	list-style: none;
}

img {
	width: 100%;
	height: auto;
}

input {
	border: none;
	box-shadow: none;
	box-sizing: border-box;
}

body {
	font-family: 'Manrope', sans-serif;
	color: var(--grey);
	font-weight: 500;
	position: relative;
	&.--is-blocked {
		overflow: hidden;
	}
}

.btn {
	background: var(--orange);
	height: 60px;
	border-radius: 5px;
	padding: 20px 25px;
	font-weight: 700;
	font-size: 18px;
	line-height: 100%;
	color: #fff;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	width: max-content;
	cursor: pointer;
}

h3 {
	font-weight: 700;
	font-size: 30px;
	line-height: 100%;
}

h5 {
	font-weight: 700;
	font-size: 24px;
	line-height: 100%;
}

.search.nav-search,
.lang.nav-lang,
.navbar {
	display: none;
}

.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	padding: 15px 10px 15px 15px;
	box-sizing: border-box;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: #fff;
}
.logo {
	width: 220px;
	height: 30px;
	
	img {
		max-width: 220px;
	}
}

.burger-menu-btn {
	width: 30px;
	height: 30px;
	background-image: url('../images/svg/menu.svg');
	background-repeat: no-repeat;
	background-position: center;
	cursor: pointer;
}

.burger-menu {
	position: fixed;
	top: 60px;
	bottom: 0;
	left: -100%;
	width: 100%;
	min-height: calc(100% - 60px);
	padding: 15px 15px 30px;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	background: #fff;
	transition: left 0.3s ease-in-out;
	overflow-y: scroll;
	
	.search {
		margin-bottom: 25px;
	}
	.links-wrapper {
		color: var(--darkGrey);
	}
	.link {
		color: var(--darkGrey);
	}
	
	&.--is-active {
		left: 0;
		transition: left 0.3s ease-in-out;

	}
	
}

.search {
	display: flex;
	width: 100%;
	max-width: 345px;
	height: 40px;
	input[type=text] {
		width: calc(100% - 40px);
		background: #E5E5E5;
		border-radius: 5px 0 0 5px;
		font-weight: 700;
		font-size: 18px;
		line-height: 25px;
		color: rgba(74, 74, 74, 0.5);
		padding: 7px 0 8px 12px;
	}
	input[type=submit] {
		width: 40px;
		background: #E5E5E5;
		border-radius: 0 5px 5px 0;
		background-image: url('../images/svg/search.svg');
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
	}
}

.links-wrapper {
	margin-bottom: 30px;
	
	h5 {
		margin-bottom: 10px;
	}
	
	&:last-child{
		margin-bottom: 0;
	}
	
	
}

.link-item {
	margin-bottom: 10px;
	&:last-child {
		margin-bottom: 0;
	}
}
.link {
	font-weight: 500;
	font-size: 18px;
	line-height: 100%;
	transition: color 0.3s ease-in-out;
	&:hover {
		color: var(--orange);
		transition: color 0.3s ease-in-out;
	}
}

.links-list {
	padding-top: 15px;
	border-top: 2px solid var(--grey);
}

.lang {
	background: var(--darkGrey);
	border-radius: 5px;
	color: #fff;
	width: 80px;
	height: 40px;	
	padding: 10px;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	img {
		width: 20px;
		height: 20px;
		margin-right: 7px;
	}
}

.main {
	padding-top: 60px;
}

.slider {
	display: flex;
	flex-direction: column;
	background: var(--darkGrey);
}
.slider-caption {
	color: #fff;
	padding: 30px 15px 60px;
	box-sizing: border-box;
	
	h3 {
		margin-bottom: 40px;
	}
}
.slider-btn {
	width: 150px;
}


.product-categories {
	display: flex;
	flex-wrap: wrap;
	padding: 15px 15px 80px;
	box-sizing: border-box;
	
}
.category {
	display: flex;
	flex-direction: column;
	width: calc((100% - 15px)/2);
	border-radius: 5px;
	background: var(--lightGrey);
	box-sizing: border-box;
	margin: 0 15px 15px 0;
	cursor: pointer;
	
	&:nth-child(2n) {
		margin-right: 0;
	}
	&:last-child {
		margin-right: 0;
	}
	
	&:hover .category-title {
		background: var(--orange);
		transition: background 0.3s ease
	}
	
}
.category-image {
	padding: 15px 15px 5px;
}
.category-title {
	border-radius: 0px 0px 5px 5px;
	background: var(--grey);
	color: #fff;
	font-weight: 700;
	font-size: 14px;
	line-height: 100%;
	padding: 10px 15px 15px;
	box-sizing: border-box;
	min-height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	transition: background 0.3s ease-in-out;
	
}

.about-company {
	background: #F2F2F2;
}

.about-company h3 {
	margin-bottom: 30px;
	letter-spacing: -0.05em;
	color: var(--darkGrey);
}

.about-text {
	padding: 30px 15px 100px;
	box-sizing: border-box;
}
.about-btn {
	width: 180px;
	margin-top: 30px;
}

.footer {
	padding: 35px 15px 20px;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	background: var(--darkGrey);
	color: #fff;
	
	
	
	.link {
		color: #fff;
		&:hover {
			color: var(--orange);
		}
	}
	
	.logo {
		margin-top: 70px;
		
		p {
			display: none;
		}
	}
	
	
}

.copyright {
	background: var(--orange);
	color: #FFFFFF;
	padding: 7px 15px 8px;
	box-sizing: border-box;
}


@media (min-width: 768px) {
	
	.slider {
		flex-direction: row;
	}
	
	.slider-image {
		width: 65%;
	}
	
	.slider-caption {
		width: 35%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	
	.category {
		width: calc((100% - 30px)/3);
		max-width: 345px;
		
		&:nth-child(2n) {
			margin-right: 15px;
		}
		&:nth-child(3n) {
			margin-right: 0;
		}
		&:last-child {
			margin-right: 0;
		}
		
	}
	
	
	
	
	
}


@media (min-width: 1024px) {
	
	
	
	.category {
		width: calc((100% - 45px)/4);
		
	
		&:nth-child(3n) {
			margin-right: 15px;
		}
		&:nth-child(4n) {
			margin-right: 0;
		}
		&:last-child {
			margin-right: 0;
		}
		
	}
	
	.about-company {
		display: flex;
		flex-direction: row-reverse;
		margin-bottom: 80px;
	}
	
	.about-image{
		width: 50%;
	}
	.about-text {
		width: 50%;
		padding: 30px 30px;
	}
	
	.footer {
		flex-direction: row;
		justify-content: space-between;
		padding: 40px 50px 40px 30px;
		
		.logo {
			margin: 0;
			order: -1;
			width: 345px;

			p {
				display: block;
				margin-top: 25px;
				font-size: 14px;
				line-height: 19px;
				color: #E5E5E5;
			}
		}
		.links-wrapper {
			margin-bottom: 0;
		}
	}
	
	
	.copyright {
		display: none;
	}
	
}

@media (min-width: 1280px) {
	p {
		font-size: 18px;
		margin-bottom: 20px;
	}
	
	h3 {
		font-size: 48px;
	}
	
	.header {
		padding: 10px 30px;
	}
	.navbar {
		margin: 0 auto;
	}
	.nav-search {
		width: 230px;
		margin-right: 15px;
	}
	
	.nav-link {
		color: var(--grey);
		margin-right: 30px;
		font-weight: 700;
		font-size: 18px;
		line-height: 25px;
		transition: color 0.3s ease-in-out;

		&:last-child {
			margin-right: 0;
		}
		&:hover {
			color: var(--orange);
			transition: color 0.3s ease-in-out;
		}
	}
	
	.search.nav-search, .lang.nav-lang, .navbar {
		display: flex;
	}
	.burger-menu-btn,
	.burger-menu {
		display: none;
	}
	
	.product-categories {
		padding: 60px 0 120px;
		max-width: 1200px;
		margin: 0 auto;
		justify-content: center;
	}
	
	.category {
		width: calc((100% - 60px)/4);
		margin-right: 20px;
		margin-bottom: 20px;
	
		&:nth-child(3n) {
			margin-right: 20px;
		}
	
		
	}
	
	.category-title {
		font-size: 18px;
		height: 80px;
	}
	
	.footer {
		padding: 80px calc((100% - 1220px)/2) 120px;
		

	}
	.links-wrapper.products-list {
		width: 50%;
		.links-list {
			display: flex;
			flex-direction: column;
			height: 160px;
			flex-wrap: wrap;
		}
		
	}

}


@media (min-width: 1920px) {
	
	.header {
		padding: 10px calc((100% - 1440px)/2);
	}
	
	
	.nav-search {
		width: 100%;
	}
	
	.slider-caption {
		padding: 0 120px 0 60px;
	}
	
	.slider-caption h3 {
		max-width: 75%;
		margin-bottom: 100px;
	}
	
	
	.product-categories {
		max-width: 1440px;
	}
	
	.about-company {
		max-width: 1640px;
		margin: 80px auto 200px;
	}
	
	.about-text {
		padding: 130px 60px 130px 100px;
	}
	
	.footer {
		padding: 80px calc((100% - 1440px)/2) 120px;
		
	}
	
}